import React, { useContext, useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { PageSettings } from "../../config/page-settings";
import background from "../../assets/img/background.jpg";
import penjualanIcon from "../../assets/img/penjualan.gif";
import pembelianIcon from "../../assets/img/pembelian.gif";
import kasIcon from "../../assets/img/kas.gif";
import laporanIcon from "../../assets/img/laporan.gif";
import { Card, CardBody } from "reactstrap";
import Swal from "sweetalert2";
import { postData, postDataNoTImeOut } from "../../config/axios";
import {
  formatDate,
  getToday,
  getUserdata,
} from "../../components/helpers/function";
import { ToastNotification } from "../../components/helpers/notification";
import moment from "moment";

const activeBg = background;
const PosHomePage = () => {
  const type = useContext(PageSettings);
  const history = useHistory();
  const [dateSystem, setdateSystem] = useState(true);

  const routeChange = (path) => {
    history.push(path);
  };
  useEffect(() => {
    type.handleSetPageSidebar(false);
    type.handleSetPageHeader(false);
    setdateSystem((getUserdata()?.tgl_system || "-") === getToday());
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const logout = () => {
    Swal.fire({
      title: "Konfirmasi Keluar !!",
      text: "Apakah anda ingin keluar ?",
      icon: "info",
      position: "top-center",
      cancelButtonText: "Tidak",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: true,
    }).then((result) => {
      if (result.isConfirmed) {
        postData("users/logout", { user_id: getUserdata().user_id })
          .then((res) => {
            window.location.href = "/nagapos-lite.com/";
            localStorage.clear();
            localStorage.setItem("islogin", "false");
          })
          .catch((err) => {
            ToastNotification(
              "info",
              err.response === undefined ? "Gagal Logout" : err.response.data
            );
            window.location.reload();
            localStorage.clear();
            localStorage.setItem("islogin", "false");
          });
      }
    });
  };
  const showModalTutupToko = () => {
    var datatoday = new Date();
    var datatodays = datatoday.setDate(
      new Date(new Date(getUserdata().tgl_system)).getDate() + 1
    );
    var todate = new Date(datatodays);
    Swal.fire({
      html:
        "<h1><b>Tanggal System berbeda dengan tanggal toko<br/> " +
        "</h1><br>" +
        "Tanggal System " +
        getUserdata().tgl_system +
        " - Tanggal Toko " +
        formatDate(moment(new Date(todate)).format("YYYY-MM-DD")),
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Ya..",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: false,
    }).then((result) => {
      if (result.isConfirmed) {
        postDataNoTImeOut("system/close", { PROSES: "close" })
          .then((res) => {
            ToastNotification("success", "Berhasil Menutup Toko");

            setTimeout(() => {
              // window.location.reload();
              window.location.href = "/nagapos-lite.com";
              localStorage.clear();
              localStorage.setItem("islogin", "false");
            }, 1000);
          })
          .catch((err) => {
            // console.log(err.response);
            err.response === undefined
              ? ToastNotification(
                  "info",
                  err.response === undefined
                    ? "Terjadi Kesalahan Saat Request Data"
                    : err.response.data
                )
              : err.response.data === "Invalid token."
              ? this.handleInvalidToken()
              : ToastNotification("info", err.response.data);
            //   setTimeout(() => {
            //   window.location.reload();
            //   localStorage.clear();
            //   localStorage.setItem("islogin", "false");
            // }, 1000);
          });
      }
    });
  };
  return (
    <React.Fragment>
      <div className="login-cover">
        <div
          className="login-cover-image"
          style={{ backgroundImage: "url(" + activeBg + ")" }}
        ></div>
      </div>
      <div
        className="login"
        style={{
          minWidth: "80%",
          minHeight: "calc(100vh - 40px)",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <div className="row col-lg-12 justify-content-center">
          <div
            className="col-lg-4 col-md-4"
            onClick={() => {
              if (dateSystem) {
                routeChange("/transaksi-penjualan");
              } else {
                showModalTutupToko();
              }
            }}
          >
            <Card
              style={{
                borderRadius: 30,
                backgroundColor: "#619680 !important",
              }}
              className="border-0 card-inverse bg-primary text-white text-center m-b-10 shadow"
            >
              <CardBody>
                <img
                  src={penjualanIcon}
                  alt="this slowpoke moves"
                  width="200"
                  class="img-fluid"
                />
                <h1>
                  <strong>Penjualan</strong>
                </h1>
              </CardBody>
            </Card>
          </div>
          <div
            className="col-lg-4 col-md-4"
            onClick={() => {
              if (dateSystem) {
                routeChange("/transaksi-pembelian");
              } else {
                showModalTutupToko();
              }
            }}
          >
            <Card
              className="border-0 card-inverse bg-primary text-white text-center m-b-10 shadow"
              style={{
                borderRadius: 30,
                backgroundColor: "#619680 !important",
              }}
            >
              <CardBody>
                <img
                  src={pembelianIcon}
                  alt="this slowpoke moves"
                  width="200"
                  class="img-fluid"
                />
                <h1>
                  <strong>Pembelian</strong>
                </h1>
              </CardBody>
            </Card>
          </div>
          <div
            className="col-lg-4 col-md-4"
            onClick={() => {
              if (dateSystem) {
                routeChange("/uang-kas");
              } else {
                showModalTutupToko();
              }
            }}
          >
            <Card
              className="border-0 card-inverse bg-primary text-white text-center m-b-10 shadow"
              style={{
                borderRadius: 30,
                backgroundColor: "#619680 !important",
              }}
            >
              <CardBody>
                <img
                  src={kasIcon}
                  alt="this slowpoke moves"
                  width="200"
                  class="img-fluid"
                />
                <h1>
                  <strong>Uang Kas</strong>
                </h1>
              </CardBody>
            </Card>
          </div>
          <div className="row col-lg-12 justify-content-center">
            <div
              className="col-lg-4 col-md-4"
              onClick={() => {
                if (dateSystem) {
                  routeChange("/laporan-transaksi-penjualan");
                } else {
                  showModalTutupToko();
                }
              }}
            >
              <Card
                style={{
                  borderRadius: 30,
                  backgroundColor: "#619680 !important",
                }}
                className="border-0 card-inverse bg-primary text-white text-center m-b-10 shadow"
              >
                <CardBody>
                  <img
                    src={laporanIcon}
                    alt="this slowpoke moves"
                    width="200"
                    class="img-fluid"
                  />
                  <h1>
                    <strong>Laporan Penjualan</strong>
                  </h1>
                </CardBody>
              </Card>
            </div>
            <div
              className="col-lg-4 col-md-4"
              onClick={() => {
                if (dateSystem) {
                  routeChange("/laporan-transaksi-pembelian");
                } else {
                  showModalTutupToko();
                }
              }}
            >
              <Card
                className="border-0 card-inverse bg-primary text-white text-center m-b-10 shadow"
                style={{
                  borderRadius: 30,
                  backgroundColor: "#619680 !important",
                }}
              >
                <CardBody>
                  <img
                    src={laporanIcon}
                    alt="this slowpoke moves"
                    width="200"
                    class="img-fluid"
                  />
                  <h1>
                    <strong>Laporan Pembelian</strong>
                  </h1>
                </CardBody>
              </Card>
            </div>
          </div>
          <div className="col-lg-12 justify-content-center row">
            <div className="col-lg-4"></div>
            <div className="col-lg-4 col-md-4" onClick={logout}>
              <Card
                className="border-0 card-inverse bg-danger text-white text-center m-b-10 shadow"
                style={{ borderRadius: 30 }}
              >
                <CardBody>
                  <h1>
                    <strong>Logout</strong>
                  </h1>
                </CardBody>
              </Card>
            </div>
            <div className="col-lg-4"></div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default PosHomePage;
